<template>
	<div class="Tmapiframe">
		<iframe class="Tmap"  frameborder="no" border="0" marginwidth="0" marginheight="0" allowtransparency="yes" :src="TmapSrc()"></iframe>
	</div>
</template>

<script>
export default {
	name: 'Tmapiframe',
	props: {
		core: Array,
		mykey: String
	},
	data () {
		return {
			
		};
	},
	created() {
		this.TmapSrc()
	},
	components: {},
	methods: {
		TmapSrc(){
			let obj = {
				type: 0,
				marker: 'coord:' + String(this.core),
				key: this.mykey,
				referer: 'myapp'
			}
			
			let arr = []
			for(let k in obj){
				arr.push(k + '=' + obj[k])
			}
			let src = `https://apis.map.qq.com/tools/poimarker?` + arr.join('&')
			console.log(src);
			return src
		}
	},
	computed: {
		
	},
}
</script>

<style scoped>
.Tmap{
	width: 100vw;
	height: calc(100vh - 46px)
}
</style>