<template>
  <div class="Tmap_marker">
    <div id="container"></div>
  </div>
</template>

<script>
export default {
	name: "Tmap_marker",
	props:{
		core: Array,
		marker: Array
	},
  data() {
    return {};
  },
  mounted() {
		this.loadScript()
  },
  components: {},
  methods: {
    // 创建腾讯地图js文件
    createTmapjs() {
			let slef = this
        let center = new TMap.LatLng(...slef.marker);//设置中心点坐标
        //初始化地图
        let map = new TMap.Map("container", {
            center: center
        });

        //初始化marker
        let marker = new TMap.MultiMarker({
            id: "marker-layer", //图层id
            map: map,
            styles: { //点标注的相关样式
                "marker": new TMap.MarkerStyle({
                    "width": 25,
                    "height": 35,
                    "anchor": { x: 16, y: 32 },
                    "src": "https://mapapi.qq.com/web/lbs/javascriptGL/demo/img/markerDefault.png"
                })
            },
            geometries: [{ //点标注数据数组
                "id": "demo",
                "styleId": "marker",
                "position": new TMap.LatLng(...slef.marker),
                "properties": {
                    "title": "marker"
                }
            }]
        })
    },
    load() {
			console.log("腾讯地图js已经加载完成");
			this.createTmapjs()
		},
		
    loadScript() {
			let self = this
			let script = document.createElement("script");
			script.charset = "utf-8";
      if (script.readyState) {
        // IE
        script.onreadystatechange = function () {
          if (
            script.readyState === "loaded" ||
            script.readyState === "complete"
          ) {
            script.onreadystatechange = null;
            self.load()
          }
        };
      } else {
        // 其他浏览器
        script.onload = function () {
          self.load()
        };
			}
      script.src = "https://map.qq.com/api/gljs?v=1.exp&key=ZM5BZ-FKTRP-7EWDG-LGJMU-JTQ2K-VEBRI";
      document.body.appendChild(script);
    },
  },
};
</script>

<style scoped>
#container{
	width: 100vw;
	height: 100vh;
}
</style>