      title="定位"
      left-text="返回"
      left-arrow
      @click-left="$router.back()"
    />
    <tmap-marker v-if="false" :core="core" :marker="marker"></tmap-marker>
    <tmapiframe :core="marker" :mykey="mykey"></tmapiframe>
  </div>
</template>

