  data() {
    return {
      core: [],
      marker: [],
      mykey: "",
    };
  },
  components: { TmapMarker, Tmapiframe },
  created() {
    let query = this.$route.query;
    console.log(this.$route.query);
    this.core = query.core;
    this.marker = query.marker;
    this.mykey = query.mykey;
  },
  methods: {},
};
</script>

